

header {
    display: grid;
   
    padding: 0.5rem 0 0 0;
    height: 100vh;
    
  }


  .content-main {
    width: 100%;
    align-self: center;
    border-radius: 5rem 2rem;
    opacity: 0.8;

    
  }
  .header-content {
    display: flex;
    flex-direction: column;
   
    width: 1100px; height: 600px;
  background-color: #1D1B26 ;
  position: relative;
  box-shadow: 10px 10px 42px 0 rgba(179, 26, 26, 0.75);
  }
  .header-content:after, .header-content:before{
    mix-blend-mode:multiply;
    filter:none;
    z-index: -1;
    content:'';
    width:calc(100% + (50px * 2));
    height:calc(100% + (50px * 2));
    position: absolute;
    display: block;
    animation: border 100s ease-in-out  ;
    transform:translateX(-50px) translateY(-50px);
  }
  @keyframes border {
    0%, 100% {
      -webkit-clip-path: polygon(0 0, calc(100% - (33.3333333333px)) calc(0% + (33.3333333333px)), 100% 100%, calc(0% + (33.3333333333px)) calc(100% - (33.3333333333px)));
              clip-path: polygon(0 0, calc(100% - (33.3333333333px)) calc(0% + (33.3333333333px)), 100% 100%, calc(0% + (33.3333333333px)) calc(100% - (33.3333333333px)));
    }
    50% {
      -webkit-clip-path: polygon(calc(0% + (33.3333333333px)) calc(0% + (33.3333333333px)), 100% 0, calc(100% - (33.3333333333px)) calc(100% - (33.3333333333px)), 0 100%);
              clip-path: polygon(calc(0% + (33.3333333333px)) calc(0% + (33.3333333333px)), 100% 0, calc(100% - (33.3333333333px)) calc(100% - (33.3333333333px)), 0 100%);
    }
  }
  
  .header-content:after{
    animation-delay: -5s;
    background-color: black;
    clip-path: polygon(0 0, calc(100% - (33.3333333333px)) calc(0% + (33.3333333333px)), 100% 100%, calc(0% + (33.3333333333px)) calc(100% - (33.3333333333px)));
  }
  .header-content:before {
    background-color: #1D1B26;
      clip-path: polygon(calc(0% + (33.3333333333px)) calc(0% + (33.3333333333px)), 100% 0, calc(100% - (33.3333333333px)) calc(100% - (33.3333333333px)), 0 100%);
  }
  
  .header-content:hover:after{
    animation-delay: -0.1s;
  }
  .header-content:hover:before, .header-content:hover:after {
  
            animation-duration: 0.2s;
  }
  @keyframes cinematicInOut {
    0% {
      text-shadow:0 0 1em white;
      opacity:0;
      margin-left:-.5em;
    }
    25% {
      text-shadow:0 0 0 white;
      opacity:1;
      margin-left:0;
    }
    75% {
      text-shadow:0 0 0 white;
      opacity:1;
      margin-left:0;
    }
    100% {
      text-shadow:0 0 1em white;
      opacity:0;
      margin-left:0;
    }
  }

  @keyframes cinematic-camera {
    from {
      perspective-origin: 60% 40%;
    }
    to {
      perspective-origin: 40% 60%;
    }
    /* 
      Move the fading to the containing element as to not break inside 3D transforms. 
      See: https://css-tricks.com/things-watch-working-css-3d/
    */
    from,
    to {
      opacity: 0;
    }
    25%,
    75% {
      opacity: 1;
    }
  }

 

  
  .content-main {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
   


  }
  .content-main h1 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
    color: yellow;
    padding-left: 10px;
    
  
  }
  @keyframes typing {
    from {
      width: 0
    }
  }
 
 
  .content-main button {
 
    align-self: center;
    color:white;
    width: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
    outline: none;
    border: none;
    font-size: 1rem;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    cursor: pointer;
    margin-top: 2.5rem;

  }

  .glow-on-hover {
    width: 220px;
    height: 50px;
    border: none;
    outline: none;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
   
}

.glow-on-hover:active {
    color: #000;

}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: slategrey;
    
    left: 0;
    top: 0;
    border-radius: 10px;
}



@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}



  .content-main p {
    font-weight: 500;
    color:gainsboro;
    width: 70ch;
    animation: typing 4s steps(22) infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    padding-left: 10px;
    font-family: monospace;
    font-size: 1rem;

    margin-bottom: 1rem;
    
    
  }
  
  @media (max-width: 768px) {
    .content-main h1 {
      font-size: 1.2rem;
      margin-bottom: 1rem;
    }
    .content-main p {
      font-size: 0.8rem;
    }
    .content-main button {
      font-size: 0.8rem;
      width: 150px;
      padding: 0.5rem 1rem;
      margin-top: 1.5rem;
    }
  }